import React, { useState } from 'react';
import { CVIProvider } from './components/cvi/components/cvi-provider';
import { Conversation } from './components/cvi/components/conversation';
import ChatInterface from './ChatInterface';

function App() {
  const [conversationUrl, setConversationUrl] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const startConversation = async () => {
    setLoading(true);
    setError(null);

    const url = 'https://tavusapi.com/v2/conversations';
    const options = {
      method: 'POST',
      headers: {
        'x-api-key': 'fa999107b41342fa8bc5255cc8c9dfe6',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        replica_id: 'rf4703150052',
        persona_id: 'pa6975faa9dc'
      })
    };

    try {
      const response = await fetch(url, options);
      const data = await response.json();

      if (response.ok && data.conversation_url) {
        setConversationUrl(data.conversation_url);
      } else {
        throw new Error(data.message || 'Failed to create conversation.');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleLeave = () => {
    setConversationUrl(null);
  };

  return (
    <CVIProvider>
      <div style={{ width: '100vw', height: '100vh', margin: 0, padding: 0 }}>
        {!conversationUrl ? (
          <div style={{ textAlign: 'center', marginTop: '100px' }}>
            <h1>Start a Tavus Conversation</h1>
            <button onClick={startConversation} disabled={loading}>
              {loading ? 'Starting...' : 'Start Conversation'}
            </button>
            {error && <p style={{ color: 'red' }}>{error}</p>}
          </div>
        ) : (
          <div style={{ display: 'flex', height: '100%' }}>
            <div style={{ flex: 2 }}>
              <Conversation
                conversationUrl={conversationUrl}
                onLeave={handleLeave}
              />
            </div>
            <div style={{ flex: 1, borderLeft: '1px solid #ccc', padding: '20px', overflowY: 'auto' }}>
              <ChatInterface conversationUrl={conversationUrl} />
            </div>
          </div>
        )}
      </div>
    </CVIProvider>
  );
}

export default App;