import React, { useState, useEffect, useRef } from 'react';
import { useDaily, useDailyEvent } from '@daily-co/daily-react';

function ChatInterface() {
  const [chatMessages, setChatMessages] = useState([]);
  const [inputText, setInputText] = useState('');
  const [conversationId, setConversationId] = useState(null);
  const [isTyping, setIsTyping] = useState(false);
  const callObject = useDaily();
  const messagesEndRef = useRef(null);

  // Scroll to bottom when messages update
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [chatMessages]);

  // Listen for avatar messages
  useDailyEvent('app-message', (event) => {
    const { data } = event;
    console.log('Received app-message:', event);

    if (data?.conversation_id && !conversationId) {
      setConversationId(data.conversation_id);
    }

    if (data?.event_type === 'conversation.utterance') {
      const utterance = data.properties;
      const sender = utterance.speaker || utterance.role;
      const text = utterance.utteranceText || utterance.speech;

      if (sender && text) {
        const lastUserMessage = chatMessages
          .slice()
          .reverse()
          .find(msg => msg.sender === 'user');

        const isDuplicate = lastUserMessage?.text?.trim() === text.trim();

        if (!isDuplicate) {
          setChatMessages(prev => [...prev, {
            sender,
            text,
            timestamp: new Date()
          }]);
        } else {
          console.log('Skipped duplicate avatar echo:', text);
        }

        setIsTyping(false);
      }
    }

    if (data?.event_type === 'conversation.replica.started_speaking') {
      setIsTyping(true);
    }

    if (data?.event_type === 'conversation.replica.stopped_speaking') {
      setIsTyping(false);
    }
  });

  // Send message to avatar
  const sendTextMessage = (userMessage) => {
    if (!callObject || !userMessage.trim() || !conversationId) return;

    const payload = {
      event_type: 'conversation.respond',
      message_type: 'conversation',
      conversation_id: conversationId,
      properties: {
        text: userMessage
      }
    };

    console.log('Sending message:', payload);
    callObject.sendAppMessage(payload);

    setChatMessages(prev => [...prev, {
      sender: 'user',
      text: userMessage,
      timestamp: new Date()
    }]);

    setInputText('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    sendTextMessage(inputText);
  };

  return (
    <div className="chat-container" style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      <div className="messages" style={{ flex: 1, overflowY: 'auto', padding: '10px' }}>
        {chatMessages.map((msg, index) => (
          <div
            key={index}
            style={{
              display: 'flex',
              justifyContent: msg.sender === 'user' ? 'flex-end' : 'flex-start',
              marginBottom: '10px'
            }}
          >
            <div
              style={{
                backgroundColor: msg.sender === 'user' ? '#007bff' : '#f1f0f0',
                color: msg.sender === 'user' ? '#fff' : '#000',
                padding: '10px',
                borderRadius: '10px',
                maxWidth: '70%',
                fontSize: '14px'
              }}
            >
              <strong>{msg.sender === 'replica' ? 'Avatar' : 'You'}:</strong><br />
              {msg.text}
              <div style={{ fontSize: '10px', marginTop: '5px', textAlign: 'right' }}>
                {msg.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
              </div>
            </div>
          </div>
        ))}
        {isTyping && (
          <div style={{ fontStyle: 'italic', color: '#888', marginLeft: '10px' }}>
            Avatar is typing...
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>

      <form onSubmit={handleSubmit} style={{ display: 'flex', padding: '10px', borderTop: '1px solid #ccc' }}>
        <input
          type="text"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          placeholder="Type your message..."
          style={{ flex: 1, padding: '10px', fontSize: '14px' }}
        />
        <button type="submit" style={{ marginLeft: '10px', padding: '10px 20px' }}>Send</button>
      </form>
    </div>
  );
}

export default ChatInterface;